.banner_image {
  picture {
    display: block;
    height: 100%;
    width: 100%;
    position: relative;

    &::after {
      box-shadow: inset 0 0 min(10vw, 150px) #000;
      content: " ";
      display: block;
      height: 100%;
      position: absolute;
      top: 0;
      width: 100%;
    }
  }

  img {
    width: 100%;
    max-width: 100%;
    height: auto;
    display: block;
    position: relative;
  }

  position: relative;

  .header {
    position: absolute;
    bottom: 0;
    width: 100%;
    background: rgba($color: #000000, $alpha: 0.5);

    h1 {
      padding-top: 0.5em;
      font-size: clamp(1.7rem, 7vw, 3.052rem);
      margin-bottom: 0.4em;
    }
    p {
      padding-bottom: 1em;
      margin: 0;
      font-size: clamp(1rem, 5vw, 1.5rem);
      line-height: 1.75em;
    }
  }
}
